<template>
	<view>
		<view class="u-flex user-box u-p-20">
			<u-form-item>
				<view class="imgwidth">
					<oss-upload></oss-upload>
				</view>
			</u-form-item>
			<view class="u-flex-1" v-if="Object.keys(userInfo).length">
				<view class="u-font-18 u-p-b-20">{{userInfo.name}}</view>
				<view class="u-font-14 u-tips-color">
					<text>邮箱: {{userInfo.email}}</text>
					<br>
					<text class="small">创建日期:{{userInfo.created_at}}</text>
				</view>
			</view>
			<view class="u-flex-1" v-else  @tap="$u.utils.isLogin()">
				<view class="u-font-18 u-p-b-20">未登录</view>
			</view>
		</view>
		
		<view class="u-m-t-20">
			<u-cell-group>
				<u-cell-item icon="account" title="修改信息" @tap="updateMsg"></u-cell-item>
			</u-cell-group>
		</view>
		<view class="u-m-t-20">
			<u-cell-group>
				<u-cell-item icon="rmb-circle" title="所有订单" @click="toOrder"></u-cell-item>
				<u-cell-item icon="star" title="商品收藏" @tap="showCollect"></u-cell-item>
				<u-cell-item icon="map" title="收货地址" @click="toAddress"></u-cell-item>
			</u-cell-group>
		</view>
		<view class="u-m-t-20" v-if="Object.keys(userInfo).length">
			<u-button type="error" :ripple="true" @click="showLoginout">退出登录</u-button>
		</view>
		<!-- 退出登录模态框 -->
		<u-modal v-model="isShowLoginout" content="确定退出登录吗？" :show-cancel-button="true" @confirm="loginOut" @cancel="showLoginout"></u-modal>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				isShowLoginout:false,
				userInfo:{}
			}
		},
		onLoad() {
			// 判断是否处于登录状态、如果未登录跳转登录页
			// this.$u.utils.isLogin()
			// 如果已经登录，拿取信息
			this.userInfo=this.vuex_user
		},
		methods: {
			// 图像预览
			previewImage(){
				uni.previewImage({
					urls:[this.userInfo.avatar_url]
				})
			},
			// 页面跳转
			jump(path){
				this.$u.route(path)
			},
			// 点击修改信息
			updateMsg(){
				if(!this.$u.utils.isLogin()){}// 如果没有登录先去登录
				else{
					this.jump('/pages/center/baseInfo')
				}
			},
			// 点击查看收藏
			showCollect(){
				if(!this.$u.utils.isLogin()){}// 如果没有登录先去登录
				else{
					this.jump('pages/center/collectGoods')
				}
			},
			// 跳转到订单列表
			toOrder(){
				if(!this.$u.utils.isLogin()){}// 如果没有登录先去登录
				else{
					this.jump('/pages/center/order')
				}
			},
			toAddress(){
				if(!this.$u.utils.isLogin()){}// 如果没有登录先去登录
				else{
					this.jump('/pages/center/address')
				}
			},
			// 出现模态框
			showLoginout(){
				this.isShowLoginout=!this.isShowLoginout
			},
			// 确定退出登录
			async loginOut(){
				// 请求api
				await this.$u.api.authLoginOut()
				
				uni.showToast({
					title:"退出成功！"
				})
				setTimeout(()=>{
					// 清除缓存token和用户信息
					this.$u.vuex("vuex_token",null)
					this.$u.vuex("vuex_user",{})
					
					// 跳转首页
					this.$u.route({type:"reLaunch",url:"pages/index/index"})
				},1500)
				
			},
			upSuccess(data){
				// 图片上传成功后 保存图片key
				this.form.avatar=data
			},
			
		}
	}
</script>

<style scoped lang="scss">
page{
	background-color: #ededed;
}
.avatar{

}
.camera{
	width: 54px;
	height: 44px;
	
	&:active{
		background-color: #ededed;
	}
}
.user-box{
	background-color: #fff;
}
.imgwidth{
	width: 70px;
	height: 70px;
	background: #FFFFFF;
	
}
</style>
